.hi-tree__menu{background:var(--color-white);width:120px;-webkit-box-shadow:0 2px 8px 0 rgba(0,0,0,0.1);box-shadow:0 2px 8px 0 rgba(0,0,0,0.1);border-radius:2px;padding:4px 0;margin:0 0 0 4px;font-size:14px}.hi-tree__menu li.menu-item{list-style:none;padding:0 15px;height:36px;line-height:36px;-webkit-transition:background 0.3s;transition:background 0.3s;cursor:pointer;text-overflow:ellipsis;white-space:nowrap;overflow:hidden}.hi-tree__menu li.menu-item:hover{background:var(--color-primary-20)}.hi-tree{color:var(--color-gray-80)}.hi-tree__searcher{position:relative}.hi-tree__searcher .searcher__result--empty{position:absolute;left:24px;top:34px;font-size:12px;color:var(--color-gray-70)}.hi-tree ul{list-style-type:none;padding:0;margin:0}.hi-tree ul:not(.root-list){padding:0 16px}.hi-tree .tree-node{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;padding-bottom:4px}.hi-tree .tree-node:focus{outline:0}.hi-tree .tree-node:focus .tree-node__title .title__text{background:var(--color-primary-20);color:var(--color-gray-80)}.hi-tree .tree-node .hi-btn--loading--icon{margin-top:-6px}.hi-tree .tree-node .hi-btn--loading--icon svg{fill:var(--color-primary)}.hi-tree .tree-node__indent{width:24px;height:100%;display:inline-block}.hi-tree .tree-node--line{position:relative}.hi-tree .tree-node--line span:last-of-type .tree-node__indent::after{content:'';position:absolute;width:50%;height:1px;background-color:var(--color-gray-70);left:calc(50% - 4px);top:50%}.hi-tree .tree-node--line .tree-node__indent{position:relative}.hi-tree .tree-node--line .tree-node__indent::before{content:'';position:absolute;width:1px;height:calc(100% + 4px);background-color:var(--color-gray-70);left:8px}.hi-tree .tree-node--line .tree-node__indent--parent-tail::before{content:none}.hi-tree .tree-node--line .tree-node__indent--tail::before{height:calc(50%);content:'' !important}.hi-tree .tree-node__title{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-flex:1;-ms-flex:1;flex:1;border-radius:1px}.hi-tree .tree-node__title .title__text{line-height:24px;font-size:14px;padding:0 4px;cursor:pointer}.hi-tree .tree-node__title .title__text:hover{background:var(--color-primary-20);color:var(--color-gray-80)}.hi-tree .tree-node__title .title__text--matched{color:var(--color-primary)}.hi-tree .tree-node__title .title__text--selected{background:var(--color-primary-40) !important}.hi-tree .tree-node__title .title__text--selected:hover{background:var(--color-primary-40) !important}.hi-tree .tree-node__title--editing{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-flex:1;-ms-flex:1;flex:1;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.hi-tree .tree-node__title--editing .save-btn{cursor:pointer;margin-right:12px;color:var(--color-primary)}.hi-tree .tree-node__title--editing .save-btn+.hi-tree .tree-node__title--editing .save-btn--disabled{cursor:not-allowed;color:var(--color-gray-70)}.hi-tree .tree-node__title--editing span{display:inline-block}.hi-tree .tree-node__title--draggable{border-top:2px solid transparent;border-bottom:2px solid transparent}.hi-tree .tree-node__title--disabled{cursor:not-allowed;color:var(--color-gray-50)}.hi-tree .tree-node__title--disabled .title__text{cursor:not-allowed}.hi-tree .tree-node__title--disabled .title__text:hover{background:var(--color-white);color:var(--color-gray-50)}.hi-tree .tree-node__title--disabled .title__text--selected{background:var(--color-white);color:var(--color-gray-50)}.hi-tree .tree-node__title--disabled .title__text--selected:hover{background:var(--color-white);color:var(--color-gray-50)}.hi-tree .tree-node__title--up{border-top:2px solid var(--color-primary)}.hi-tree .tree-node__title--in{background:var(--color-primary);color:var(--color-white)}.hi-tree .tree-node__title--down{border-bottom:2px solid var(--color-primary)}
