.hi-search{display:inline-block}.hi-search_input{display:-webkit-box;display:-ms-flexbox;display:flex}.hi-search_input .hi-input__inner{border-top-right-radius:0;border-bottom-right-radius:0}.hi-search_input .hi-input__inner:hover .hi-btn{border-left-color:transparent}.hi-search_input .hi-btn{border-left-color:transparent;border-top-left-radius:0;border-bottom-left-radius:0}.hi-search_input .hi-btn:hover:not(.disabled){border-color:var(--color-primary)}.hi-search_input .hi-btn:focus:not(.disabled){border-color:var(--color-primary)}.hi-search_input .hi-btn:focus{-webkit-box-shadow:none;box-shadow:none}.hi-search .hi-icon.icon-search{color:var(--color-primary)}.hi-search_dropdown{width:100%;height:252px;font-size:12px;color:var(--color-black);overflow-x:hidden;overflow-y:auto;background-color:var(--color-white);-webkit-box-shadow:0 1px 6px rgba(0,0,0,0.2);box-shadow:0 1px 6px rgba(0,0,0,0.2);border-radius:2px;-webkit-box-sizing:border-box;box-sizing:border-box;text-align:left;margin-top:-4px}.hi-search_dropdown ul{margin:0;padding:0}.hi-search_dropdown li{margin:0;list-style:none}.hi-search_dropdown p{margin:0;padding:0}.hi-search_dropdown--item{-webkit-box-flex:0;-ms-flex:none;flex:none;display:block;line-height:36px;margin:0;font-weight:400;cursor:pointer;list-style:none;-webkit-transition:all 0.3s ease;transition:all 0.3s ease;text-overflow:ellipsis;white-space:nowrap}.hi-search_dropdown--item_normal{display:block;line-height:36px;padding:0 12px}.hi-search_dropdown--item_normal:hover:not(.hi-search_dropdown--item-title){background-color:var(--color-primary-20)}.hi-search_dropdown--item--focus{background-color:var(--color-primary-20)}.hi-search_dropdown--item-title{color:var(--color-gray-50);font-size:14px}.hi-search_dropdown--item-history{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center;padding:0 12px;color:var(--color-gray-50);font-size:14px}.hi-search_dropdown--item-nodata{height:260px;line-height:260px;text-align:center;color:var(--color-gray-50);font-size:14px}.hi-search_dropdown--item__name-hightlight{color:var(--color-primary)}.hi-search_dropdown--more{line-height:36px;margin:0;font-weight:400;cursor:pointer;font-size:12px;text-align:center}
