.hi-switch{display:inline-block;min-width:48px;height:20px;border-radius:10px;cursor:pointer;position:relative;vertical-align:middle;background:var(--color-primary)}.hi-switch:focus{outline:0;z-index:1;-webkit-box-shadow:0 0 0 2px var(--color-primary-30);box-shadow:0 0 0 2px var(--color-primary-30)}.hi-switch--closed,.hi-switch--disabled{background:var(--color-gray-50)}.hi-switch::after{content:'';width:16px;height:16px;background:var(--color-white);position:absolute;border-radius:50%;top:2px;left:100%;-webkit-transform:translateX(-100%);transform:translateX(-100%);margin-left:-2px;-webkit-transition:all 200ms linear;transition:all 200ms linear}.hi-switch__text{color:var(--color-white);font-size:12px;position:absolute;top:0;line-height:20px;left:6px;-webkit-transition:all 200ms linear;transition:all 200ms linear;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.hi-switch--closed::after{left:2px;-webkit-transform:translateX(2px);transform:translateX(2px)}.hi-switch--closed .hi-switch__text{left:auto;right:6px}.hi-switch--disabled{cursor:not-allowed}
