.hi-tooltip{position:relative;display:inline-block}.hi-tooltip-base{border-radius:2px;background:var(--color-gray-80);font-size:12px;color:var(--color-white);padding:6px 12px;line-height:20px;white-space:nowrap;-webkit-transition:all 0.5s;transition:all 0.5s}.hi-tooltip-base::after{content:'';position:absolute;width:5px;height:5px;background:var(--color-gray-80)}.hi-tooltip-top{position:relative;margin-bottom:5px}.hi-tooltip-top::after{bottom:-2px;left:50%;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg)}.hi-tooltip-right{position:relative;margin-left:5px}.hi-tooltip-right::after{left:-2px;top:50%;-webkit-transform:translateY(-50%) rotate(45deg);transform:translateY(-50%) rotate(45deg)}.hi-tooltip-bottom{position:relative;margin-top:5px}.hi-tooltip-bottom::after{top:-2px;left:50%;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg)}.hi-tooltip-left{position:relative;margin-right:5px}.hi-tooltip-left::after{right:-2px;top:50%;-webkit-transform:translateY(-50%) rotate(45deg);transform:translateY(-50%) rotate(45deg)}.hi-tooltip-top-left{position:relative;margin-bottom:5px}.hi-tooltip-top-left::after{bottom:-2px;left:20%;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg)}.hi-tooltip-top-right{position:relative;margin-bottom:5px}.hi-tooltip-top-right::after{bottom:-2px;right:10%;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg)}.hi-tooltip-bottom-right{position:relative;margin-top:5px}.hi-tooltip-bottom-right::after{top:-2px;right:10%;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg)}.hi-tooltip-bottom-left{position:relative;margin-top:5px}.hi-tooltip-bottom-left::after{top:-2px;left:20%;-webkit-transform:translateX(-50%) rotate(45deg);transform:translateX(-50%) rotate(45deg)}.hi-tooltip-left-top{position:relative;margin-right:5px}.hi-tooltip-left-top::after{right:-2px;top:15%}.hi-tooltip-left-bottom{position:relative;margin-right:5px}.hi-tooltip-left-bottom::after{right:-2px;bottom:15%}.hi-tooltip-right-top{position:relative;margin-left:5px}.hi-tooltip-right-top::after{left:-2px;top:15%}.hi-tooltip-right-bottom{position:relative;margin-left:5px}.hi-tooltip-right-bottom::after{left:-2px;bottom:15%}
