.hi-carousel{width:100%;color:#fff;overflow:hidden;position:relative;outline:none}.hi-carousel__container{height:100%;-webkit-transition:all 500ms;transition:all 500ms}.hi-carousel__dots{position:absolute;bottom:24px;margin:0;padding:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);max-width:50%;display:-webkit-box;display:-ms-flexbox;display:flex;z-index:2}.hi-carousel__dot{width:24px;height:4px;background:rgba(255,255,255,0.4);border-radius:2px;display:inline-block;margin-left:4px;cursor:pointer;-webkit-transition:all 1s;transition:all 1s}.hi-carousel__dot--active:not(.hi-carousel__dot--sign){background:#fff;width:48px}.hi-carousel__dot--sign{width:24px}.hi-carousel__arrows{position:absolute;width:100%;list-style:none;margin:0;padding:0;top:50%;-webkit-transform:translateY(-28px);transform:translateY(-28px);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;visibility:hidden;height:0;z-index:2}.hi-carousel__arrows--show{visibility:visible}.hi-carousel__arrow{width:56px;height:56px;background:rgba(0,0,0,0.25);border-radius:50%;text-align:center;line-height:56px;color:#fff;font-size:32px;margin-left:24px;cursor:pointer}.hi-carousel__arrow:hover{background:rgba(0,0,0,0.45)}.hi-carousel__arrow:last-child{margin-right:24px}.hi-carousel__pages{width:46px;height:22px;border-radius:16px;position:absolute;bottom:24px;right:24px;background:rgba(0,0,0,0.5);line-height:22px;font-size:14px;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;opacity:0.5;z-index:2}
