.hi-breadcrumb{font-size:12px}.hi-breadcrumb ul{margin-top:0;margin-bottom:0;padding-left:0;font-size:0}.hi-breadcrumb li{margin:0;list-style:none}.hi-breadcrumb__content{cursor:pointer}.hi-breadcrumb__content .hi-icon{margin:0 4px}.hi-breadcrumb__separator{margin:0 8px;color:var(--color-gray-30)}.hi-breadcrumb__item{display:inline-block}.hi-breadcrumb__item a{color:var(--color-gray-80)}.hi-breadcrumb__item .hi-breadcrumb__content:hover{color:var(--color-primary)}.hi-breadcrumb__item:last-child .hi-breadcrumb__content{cursor:text;color:var(--color-gray-80)}.hi-breadcrumb__item:last-child .hi-breadcrumb__separator{display:none}
