.hi-drawer{color:var(--color-gray-80);font-size:14px}.hi-drawer__mask{position:fixed;top:0;left:0;right:0;bottom:0;height:0;z-index:1000;background:rgba(0,0,0,0.45);opacity:0;-webkit-transition:opacity 0.3s, height 0s 0.3s;transition:opacity 0.3s, height 0s 0.3s}.hi-drawer__mask--visible{opacity:1;height:100%;-webkit-transition:opacity 0.3s;transition:opacity 0.3s}.hi-drawer__wrapper{width:360px;height:100%;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;background:var(--color-white);z-index:1000;top:0;position:fixed;border-radius:2px;-webkit-box-shadow:0 5px 15px 0 rgba(0,0,0,0.1);box-shadow:0 5px 15px 0 rgba(0,0,0,0.1);-webkit-transform:translateX(100%);transform:translateX(100%);-webkit-transition:-webkit-transform 0.3s;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.hi-drawer__wrapper:focus{outline:0}.hi-drawer__wrapper--left{left:0;-webkit-transform:translateX(-100%);transform:translateX(-100%)}.hi-drawer__wrapper--right{right:0;-webkit-transform:translateX(100%);transform:translateX(100%)}.hi-drawer__wrapper--visible{-webkit-transform:none;transform:none}.hi-drawer__header{font-size:16px;color:var(--color-gray-80);font-weight:500;height:54px;border-bottom:1px solid var(--color-gray-30);-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 24px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.hi-drawer__content{-webkit-box-sizing:border-box;box-sizing:border-box;overflow:auto;-webkit-box-flex:1;-ms-flex:1;flex:1;padding:24px}.hi-drawer__footer{border-top:1px solid var(--color-gray-30);padding:12px;-ms-flex-negative:0;flex-shrink:0}
