.hi-alert{position:relative;padding:9px 11px;border-radius:2px;font-size:14px;min-width:280px;display:block;-webkit-box-align:start;-ms-flex-align:start;align-items:flex-start}.hi-alert__title{display:-webkit-box;display:-ms-flexbox;display:flex;font-size:14px;-webkit-box-align:center;-ms-flex-align:center;align-items:center;line-height:22px}.hi-alert__title .hi-icon{height:16px;width:16px;margin-right:8px}.hi-alert__content{margin:2px 24px 0 24px;font-weight:400;font-size:12px;line-height:18px;color:var(--color-gray-80)}.hi-alert__close-btn{position:absolute;top:12px;right:12px;cursor:pointer;color:var(--color-gray-70)}.hi-alert--primary{background-color:var(--color-primary-20);color:var(--color-primary);border:1px solid var(--color-primary-40)}.hi-alert--warning{background-color:var(--color-warning-20);color:#ffb31a;border:1px solid var(--color-warning-40)}.hi-alert--success{background-color:var(--color-success-20);color:#1da553;border:1px solid var(--color-success-40)}.hi-alert--danger{background-color:var(--color-danger-20);color:#f44343;border:1px solid var(--color-danger-40)}
