.hi-preview{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.54);z-index:9999;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.hi-preview__image{-webkit-transform:all 0.3s;transform:all 0.3s}.hi-preview__image:hover{cursor:move}.hi-preview-toolbar{position:absolute;bottom:20px;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background:#fff;padding:0 32px;height:48px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;border-radius:28px}.hi-preview-toolbar .hi-icon{margin:0 12px;font-size:20px;cursor:pointer}.hi-preview__close{position:absolute;right:10px;top:10px;width:36px;height:36px;line-height:1;border-radius:50%;text-align:center;cursor:pointer}.hi-preview__wrapper-arrow{position:absolute;font-size:72px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;color:#fff;width:100%;z-index:-1}.hi-preview__wrapper-arrow .hi-icon{cursor:pointer}.hi-preview__count{position:absolute;top:12px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);background:#fff;padding:4px 12px;border-radius:16px;min-width:48px;text-align:center}
