.hi-badge{display:inline-block;position:relative}.hi-badge-value{position:absolute;top:-10px;right:0;-webkit-transform:translateX(50%);transform:translateX(50%);height:16px;padding:0 4px;min-width:8px;border:2px solid var(--color-white);border-radius:10px;line-height:16px;background:#f44343;font-size:12px;color:var(--color-white);letter-spacing:0;text-align:center;z-indx:1}.hi-badge-value.hi-hide{display:none}.hi-badge-dot{position:absolute;width:8px;height:8px;top:-5px;right:-5px;background:#f44343;border:1px solid var(--color-white);border-radius:50%;z-index:1}.hi-badge-dot.hi-hide{display:none}
