.hi-pagination{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.hi-pagination__text{line-height:32px;color:var(--color-gray-80)}.hi-pagination__total{margin-right:16px}.hi-pagination__jumper{margin-left:24px}.hi-pagination__span{display:inline-block;margin:0 4px}.hi-pagination__span .hi-select__input{border:1px solid var(--color-gray-20)}.hi-pagination__jumper-input{display:inline-block;margin:0 20px 0 8px;color:var(--color-gray-80)}.hi-pagination__jumper-input .hi-input{width:64px}.hi-pagination__jumper-input .hi-input__inner{border:1px solid var(--color-gray-20)}.hi-pagination__jumper-input .hi-input__text{text-align:center}.hi-pagination__jumper-input .hi-input__fix-box{display:none}.hi-pagination__item{float:left}.hi-pagination__item+.hi-pagination__item{margin-left:8px}.hi-pagination__item-break{width:32px;text-align:center}.hi-pagination__item>span{-webkit-box-sizing:border-box;box-sizing:border-box;display:inline-block;min-width:32px;padding:0 4px;height:32px;border:1px solid var(--color-gray-20);border-radius:2px;font-size:14px;line-height:30px;text-decoration:none;text-align:center;color:var(--color-gray-80);cursor:pointer}.hi-pagination__item>span:hover{border:1px solid transparent;-webkit-transition:border-color .15s ease-in,color .15s ease-in;transition:border-color .15s ease-in,color .15s ease-in}.hi-pagination__item--active>span{border:1px solid transparent}.hi-pagination__item--disabled>span{border:1px solid var(--color-gray-20);color:var(--color-gray-20);cursor:default}.hi-pagination__item--disabled>span:hover{border:1px solid var(--color-gray-20)}.hi-pagination .hi-dropdown-menu{min-width:0}.hi-pagination__item:not(.hi-pagination__item--disabled)>span:hover{border-color:rgba(var(--color-primary), 0.8);color:var(--color-primary)}.hi-pagination__item:focus{outline:none}.hi-pagination__item:focus:no