.hi-modal{color:var(--color-black);font-size:14px}.hi-modal__mask{position:fixed;top:0;left:0;right:0;bottom:0;height:0;z-index:1000;background:rgba(0,0,0,0.45);opacity:0;-webkit-transition:opacity 0.3s, height 0s 0.3s;transition:opacity 0.3s, height 0s 0.3s}.hi-modal__mask--visible{opacity:1;height:100%;-webkit-transition:opacity 0.3s;transition:opacity 0.3s}.hi-modal__outter{z-index:1000;top:0;left:0;right:0;bottom:0;position:fixed;outline:0;overflow:auto}.hi-modal__container{position:relative;top:100px;outline:0;padding-bottom:20px}.hi-modal__wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;background:var(--color-white);border-radius:2px;margin:0 auto;-webkit-box-shadow:0 5px 15px 0 rgba(0,0,0,0.1);box-shadow:0 5px 15px 0 rgba(0,0,0,0.1)}.hi-modal__wrapper--default{width:600px;max-height:600px;min-height:240px}.hi-modal__wrapper--large{width:800px;max-height:600px;min-height:240px}.hi-modal__wrapper--small{width:360px;max-height:600px;min-height:240px}.hi-modal__header{font-size:16px;color:var(--color-black);font-weight:600;height:54px;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 24px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-negative:0;flex-shrink:0}.hi-modal__header--divided{border-bottom:1px solid var(--color-gray-20)}.hi-modal__content{-webkit-box-flex:1;-ms-flex:1;flex:1;-webkit-box-sizing:border-box;box-sizing:border-box;overflow:auto;padding:24px}.hi-modal__footer{height:54px;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 24px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end;-ms-flex-negative:0;flex-shrink:0}.hi-modal__footer--divided{border-top:1px solid var(--color-gray-20)}.hi-modal .modal-transition-enter{opacity:0;-webkit-transform:scale(0.4);transform:scale(0.4)}.hi-modal .modal-transition-enter-done{opacity:1;-webkit-transform:scale(1);transform:scale(1);-webkit-transition:opacity 0.3s, -webkit-transform 0.3s;transition:opacity 0.3s, -webkit-transform 0.3s;transition:opacity 0.3s, transform 0.3s;transition:opacity 0.3s, transform 0.3s, -webkit-transform 0.3s}.hi-modal .modal-transition-exit{opacity:1;-webkit-transform:scale(1);transform:scale(1)}.hi-modal .modal-transition-exit-done{opacity:0;-webkit-transform:scale(0.4);transform:scale(0.4);-webkit-transition:opacity 0.3s, -webkit-transform 0.3s;transition:opacity 0.3s, -webkit-transform 0.3s;transition:opacity 0.3s, transform 0.3s;transition:opacity 0.3s, transform 0.3s, -webkit-transform 0.3s}
